.youtube-home {
  display: grid;
  height: 100vh;
  grid-template-columns: 16% 82%;
  background-color: #f5f5f5;
  grid-column-gap: 20px;
  padding: 15px;
  overflow: scroll;
  width: 100%;
}

@media screen and (max-width: 560px) {
  .youtube-home {
    grid-template-columns: 100%;
    position: fixed;
    width: 100%;
  }
}
